section.theory-bg {
    width: 100vw;
    height: 100vh;
    background-image: url('../../images/eafc_imh.webp');
    background-size: cover;
    background-position: center -15%; /* Adjust the vertical position */
    background-attachment: fixed; /* Keeps the background image fixed */
    margin: 0;
    filter: brightness(20%);
    z-index: -1;
    position: fixed;
    top: 0;
    left: 0;
    background-repeat: repeat;
}

main.assist-sect{
    background-color: rgba(0, 0, 0, 0.853);
    color: white;
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
    text-align: justify;
    padding: 1rem;
    margin-top: 2rem;
}

a{
    color: aquamarine;
}

ul{
    list-style: none;
    
}

li{
    margin-top: 1rem;
}

section.filter-sect{
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    display: grid;
    grid-template-columns: repeat(3, 30%);
    gap: 1rem;
    height: auto;
    margin-bottom: 1rem;
}

.fil-btn{
    border: white 1px solid;
    background-color: black;
    color: white;
    padding: 0.5rem;
}
.fil-btn:hover{
   cursor: pointer;
   background-color: rgb(131, 131, 131);
}

